import React from 'react'
import { Link } from 'react-router-dom'
export default function Book({item}){
    const booksContainerStyle ={
        display : "flex",
        flexDirection: "column",
        width :"300px",
    };

    const booksInfoStyle ={
        display: "flex",
        flexDirection: "Column",
        alignItems: "center",
        text: "center",
        color: "white",
        TextDecoration: "none",
    };

    return<div style={booksContainerStyle}>
        <Link to={`/view/${item.id}`} style={booksInfoStyle}>
        <img src={item.cover} width="200" alt={item.title}></img>
        <div>{item.title}</div>
        </Link>
    </div>
}